
export default {
  namespace: true,
  state: {
    userInfo: {}, // 用户信息
    currentProject: {}, // 当前项目
    listProject: [] // 所有项目
  },
  mutations: {
    SET_USER_INFO(state, value) {
      state.userInfo = value
    },
    SET_CURRENT_PROJECT(state, value) {
      state.currentProject = value
    },
    SET_LIST_PROJECT(state, value) {
      state.listProject = value
    }
  },
  actions: {
    setUserInfo({ commit }, data) {
      commit('SET_USER_INFO', data)
    },
    setCurrentProject({ commit }, data) {
      commit('SET_CURRENT_PROJECT', data)
    },
    setListProject({ commit }, data) {
      commit('SET_LIST_PROJECT', data)
    }
  }
}
