import router from './router'
import { getToken } from '@/utils/common'
import bridgeServer from '@/common/jsBridgeServer'
import getPageTitle from '@/utils/get-page-title'
const whiteList = ['/login', '/qrcode', '/scan', '/photo']
const backHoav = ['/', '/ticket', '/person']
router.beforeEach((to, from, next) => {
  document.title = getPageTitle(to.meta.title)
  // 为模拟单页 首页不允许路由返回事件，在此拦截所有路由设置1允许路由返回事件
  if (backHoav.includes(to.path)) {
    // eslint-disable-next-line new-cap
    new bridgeServer().SetViewParams({back_behavior: '2'})
  } else {
    // eslint-disable-next-line new-cap
    new bridgeServer().SetViewParams({back_behavior: '1'})
  }
  const token = getToken()
  if (token) {
    next()
  } else {
    if (whiteList.includes(to.path) || to.path.startsWith('/visitor')) {
      next()
    } else {
      console.log('未登录跳转')
      next('/login')
    }
  }
})
