import Vue from 'vue'
import VueI18n from 'vue-i18n'
import { Locale } from 'vant'

import vantZhCN from 'vant/lib/locale/lang/zh-CN'
import vantEnUS from 'vant/lib/locale/lang/en-US'

import enLocale from './en'
import zhLocale from './zh'
import store from '@/store'

Vue.use(VueI18n)

const messages = {
  en: {
    ...enLocale,
    ...vantEnUS
  },
  zh: {
    ...zhLocale,
    ...vantZhCN
  }
}
function getLanguage() {
  const chooseLanguage = store.getters.getLanguage
  if (chooseLanguage) return chooseLanguage
  const language = (navigator.language || navigator.browserLanguage).toLowerCase()
  const locales = Object.keys(messages)
  for (const locale of locales) {
    if (language.indexOf(locale) > -1) {
      return locale
    }
  }
  return 'zh'
}

function vantLocales(lang) {
  if (lang === 'en') {
    Locale.use(lang, vantEnUS)
  } else if (lang === 'zh') {
    Locale.use(lang, vantZhCN)
  }
}

const i18n = new VueI18n({
  locale: getLanguage(), // 设置默认语言
  messages // 设置资源文件对象
})

export { i18n, vantLocales }
