import {Toast, Notify, Dialog} from 'vant'
import { getToken } from '@/utils/common'

const baseUrl = process.env.VUE_APP_BASE_API

async function post(url, params, message = '加载中...') {
  const headers = {
    'Content-Type': 'application/json;charset=UTF-8'
  }
  return await requestPost(url, JSON.stringify(params), headers, message)
}

async function postFile(url, params, message = '上传中...') {
  return await requestPost(url, params, {}, message)
}

async function requestPost(url, params, headers, message) {
  const toast = Toast.loading({
    duration: 0,
    forbidClick: true,
    loadingType: 'spinner',
    message: message
  })
  const sendUrl = baseUrl + url
  console.log('SEND:' + sendUrl, params)
  try {
    const token = getToken()
    if (token) {
      headers.Authorization = token
    }
    const data = await fetch(sendUrl, {
      method: 'post',
      body: params,
      headers: headers
    })
    const success = data.ok && data.status === 200
    if (!success) {
      console.log(data)
      errorHandle(data.status, data.statusText)
      return Promise.reject(data.statusText)
    }
    const payload = await data.json()
    console.log('RECV:', payload)
    if (!payload.result) {
      Notify(payload.data)
      return Promise.reject(payload.data)
    } else {
      return Promise.resolve(payload.data)
    }
  } catch (e) {
    Notify('请求失败')
  } finally {
    toast.clear()
  }
}

/**
 * 请求失败后的错误统一处理
 * @param {Number} status 请求失败的状态码
 * @param message
 */
const errorHandle = (status, message) => {
  switch (status) {
    // 401: 未登录
    case 401:
      Dialog.alert({
        title: '系统通知',
        message: '服务器返回401：没有权限，请重新登录。'
      }).then(() => {
        location.replace('/#/login')
      })
      break
    // 404请求不存在
    case 403:
      Notify('服务器返回403：权限不足')
      break
    case 404:
      Notify('服务器返回404：链接不存在')
      break
    case 500:
      Notify('服务器返回500：内部服务器错误')
      break
    // 其他错误，直接抛出错误提示
    default:
      Notify(message)
      break
  }
}

export default {
  post: post,
  postFile: postFile
}
