import bridgeServer from '@/common/jsBridgeServer'
import eventBus from '@/utils/eventBus'
import router from '@/router'
const isKong = function isKong(str) {
  if (str == '' || str == '&nbsp;' || undefined == str || str == null || str == 'null' || str == 'undefined') {
    return true
  }
  return false
}
// 日期格式化
const dateFormat = function formatTime(date, format) {
  if (typeof (date) === 'string') {
    date = new Date(date)
  }

  const year = date.getFullYear()
  let month = date.getMonth() + 1
  let day = date.getDate()

  let hour = date.getHours()
  let minute = date.getMinutes()
  let second = date.getSeconds()

  if (month < 10) {
    month = '0' + month
  }
  if (day < 10) {
    day = '0' + day
  }
  if (hour < 10) {
    hour = '0' + hour
  }
  if (minute < 10) {
    minute = '0' + minute
  }
  if (second < 10) {
    second = '0' + second
  }

  if (format === 'yyyy') {
    return year
  } else if (format === 'yyyy-mm') {
    return year + '-' + month
  } else if (format === 'yyyy-mm-dd') {
    return year + '-' + month + '-' + day
  } else if (format === 'yyyy-mm-dd hh:mi') {
    return year + '-' + month + '-' + day + ' ' + hour + ':' + minute
  } else if (format === 'yyyy-mm-dd hh:mi:ss') {
    return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
  } else if (format === 'mm-dd') {
    return month + '-' + day
  } else if (format === 'hh24:mi:ss') {
    return hour + ':' + minute + ':' + second
  } else if (format === 'hh24:mi') {
    return hour + ':' + minute
  } else if (format === 'yyyy年mm月dd日') {
    return year + '年' + month + '月' + day + '日'
  } else {
    return year + '-' + month + '-' + day
  }
}
// 金额大写格式化
const cnMoneyFormat = function cnMoneyFormat(money) {
  // var cnMoney = '零元整'
  let strOutput = ''
  let strUnit = '仟佰拾亿仟佰拾万仟佰拾元角分'
  money += '00'
  const intPos = money.indexOf('.')
  if (intPos >= 0) {
    money = money.substring(0, intPos) + money.substr(intPos + 1, 2)
  }
  money = '00000' + money
  console.log(money)
  strUnit = strUnit.substr(strUnit.length - money.length)
  for (let i = 0; i < money.length; i++) {
    strOutput += '零壹贰叁肆伍陆柒捌玖'.substr(money.substr(i, 1), 1) + strUnit.substr(i, 1)
  }
  // cnMoney = strOutput.replace(/零角零分$/, '整').replace(/零[仟佰拾]/g, '零').replace(/零{2,}/g, '零').replace(/零([亿|万])/g, '$1').replace(/零+元/, '元').replace(/亿零{0,3}万/, '亿').replace(/^元/, "零元");
  return strOutput
}
// 日期的前几天或者后几天(Date)
const addDays = function(date, days) {
  if (typeof (date) === 'string') {
    date = new Date(date)
  }
  const newDate = new Date(date.getTime() + days * 24 * 60 * 60 * 1000)
  return newDate
}

const addMonths = function(date, month) {
  if (typeof (date) === 'string') {
    date = new Date(date)
  }
  console.info('date:' + date)
  const newDate = new Date(date.getFullYear(), date.getMonth() + month)
  return newDate
}
const addYears = function(date, year) {
  if (typeof (date) === 'string') {
    date = new Date(date)
  }
  console.info('date:' + date)
  const newDate = new Date(date.getFullYear() + year)
  return newDate
}
const setDate = function(date, year, month) {
  if (typeof (date) === 'string') {
    date = new Date(date)
  }
  console.info('date:' + date)
  const newDate = new Date(date.getFullYear() + year, month)
  return newDate
}

// 文件下载
const downloadFile = function downloadFile(data, fileName) {
  const blob = new Blob([data])
  if ('download' in document.createElement('a')) { // 非IE下载
    const elink = document.createElement('a')
    elink.download = fileName
    elink.style.display = 'none'
    elink.href = window.URL.createObjectURL(blob)
    document.body.appendChild(elink)
    elink.click()
    window.URL.revokeObjectURL(elink.href) // 释放URL 对象
    document.body.removeChild(elink)
  } else { // IE10+下载
    navigator.msSaveBlob(blob, fileName)
  }
}

const routerUrl = function(url, data) {
  let queryStr = ''
  if (data) {
    const queryArr = []
    for (const i in data) {
      queryArr.push(i + '=' + data[i])
    }
    queryStr = '?' + queryArr.join('&')
  }
  let slashUrl = url
  let actual = url
  if (url.substring(0, 1) !== '/') {
    actual = `/${url}`
  } else {
    slashUrl = url.substring(1, url.length)
  }
  // window.location.href="/?usePrivacy#/usePrivacy"
  window.location.href = `/?${slashUrl}#${actual}${queryStr}`
}

const isPwa = function () {
  return ('serviceWorker' in navigator) && (navigator.serviceWorker.controller !== null)
}

const scan = async function () {
  if (isPwa()) {
    routerUrl('/scan')
  } else {
    // eslint-disable-next-line new-cap
    const res = await new bridgeServer().Scan()
    eventBus.$emit('ScanResult', res)
  }
}

const closePage = async function (refresh) {
  if (isPwa()) {
    router.back()
  } else {
    // eslint-disable-next-line new-cap
    await new bridgeServer().Close(refresh)
  }
}

export default {
  isKong: isKong,
  dateFormat: dateFormat,
  cnMoneyFormat: cnMoneyFormat,
  addDays: addDays,
  addMonths: addMonths,
  addYears: addYears,
  setDate: setDate,
  downloadFile: downloadFile,
  routerUrl: routerUrl,
  isPwa: isPwa,
  scan: scan,
  closePage: closePage
}
